import { getCsrfToken, signIn } from 'next-auth/react';
import { GetServerSideProps } from 'next';

export const getServerSideProps: GetServerSideProps = async (ctx) => {
  const csrfToken = await getCsrfToken(ctx);
  return { props: { csrfToken } } as any;
};

export default function SignIn({ csrfToken }: { csrfToken?: string }) {
  return (
    <main className="min-h-screen flex items-center justify-center p-6">
      <form
        method="post"
        onSubmit={(e) => {
          e.preventDefault();
          const form = e.currentTarget as HTMLFormElement;
          const data = new FormData(form);
          signIn('credentials', {
            redirect: true,
            callbackUrl: '/',
            username: data.get('username') as string,
            password: data.get('password') as string,
          });
        }}
        className="w-full max-w-sm space-y-4 bg-white p-6 rounded shadow"
      >
        <input name="csrfToken" type="hidden" defaultValue={csrfToken} />
        <h1 className="text-xl font-semibold">Sign in</h1>
        <input name="username" type="text" placeholder="Username" className="w-full border px-3 py-2 rounded" />
        <input name="password" type="password" placeholder="Password" className="w-full border px-3 py-2 rounded" />
        <button type="submit" className="w-full bg-black text-white py-2 rounded">Sign in</button>
      </form>
    </main>
  );
}

